<template>
  <div>
    <!-- 骨架屏 -->
    <!-- <div style="background:#fff;margin-bottom:10px;padding:10px;" v-show="!showSkeleton">
      <van-skeleton title :row="3" />
    </div>
    <div style="background:#fff;margin-bottom:10px;padding:10px;" v-show="!showSkeleton">
      <van-skeleton title :row="3" />
    </div>
    <div style="background:#fff;margin-bottom:10px;padding:10px;" v-show="!showSkeleton">
      <van-skeleton title :row="3" />
    </div> -->
    <div v-if="showNoData===true" class="flexBox">
      <span style="color:grey">没有更多了...</span>
    </div>
    <van-pull-refresh v-model="refreshing" @refresh="onRefresh" v-show="showSkeleton">
      <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
        <!-- <van-cell :key="item" v-for="item in list" :title="item" /> -->

        <div v-for="(item,index) in list" :key="index" style="margin-bottom:10px;">
          <div style="background:#fff;padding:10px;padding-bottom:0px;">
            <div class="boxFlex borderBottom">
              <div>
                <span>人员：</span>
                <span>{{item.responsiblePerson}}</span>
              </div>
            </div>
            <div class="boxFlex">
              <div class="rowleft">
                <!-- <div>
                  <span>人员：</span>
                  <span>{{item.responsiblePerson}}</span>
                </div>-->
                <!-- <div> -->
                <span>废液：</span>
                <span>{{item.amount}}桶</span>
                <!-- </div> -->
              </div>
              <div class="rowright">
                <!-- <div> -->
                <span>电话：</span>
                <span style="color:blue" @click="call(item.phone)">{{item.phone}}</span>
                <!-- </div> -->
              </div>
            </div>
            <div class="boxFlex">
              <div>
                <span>转运至：</span>
                <span>{{item.destination}}</span>
              </div>
            </div>
          </div>
          <van-collapse v-model="activeNames" style="background:#fff">
            <van-collapse-item
              :title="item.generationTime"
              title-class="titleColor"
              value-class="rightTitleColor"
              :name="index"
              value="图片"
            >
              <div class="imgBox">
                <div v-for="(img,i) in item.photo" :key="i" class="box">
                  <div class="imageSizeWaste" @click="imageShow(urlPic+img,i)">
                    <img :src="urlPic+img" alt />
                  </div>
                </div>
              </div>
            </van-collapse-item>
          </van-collapse>
        </div>
      </van-list>
    </van-pull-refresh>
  </div>
</template>
<script>
import Vue from "vue";
import AppBace from "../../../static/ajax";
import { UrlPic } from "../../../static/js/requestUrl";

import {
  Skeleton,
  ImagePreview,
  Collapse,
  CollapseItem,
  PullRefresh
} from "vant";
// import {  } from "vant";
import { List } from "vant";
import { Image } from "vant";
import { Toast, Picker, Popup } from "vant";
Vue.use(Toast)
  .use(Picker)
  .use(Popup);

Vue.use(Image);
Vue.use(List);
Vue.use(Collapse);
Vue.use(CollapseItem);
Vue.use(Skeleton);
Vue.use(ImagePreview);
Vue.use(PullRefresh);
export default {
  data() {
    return {
      showSkeleton: false,
      activeNames: ["1"],
      list: [],
      loading: false,
      finished: false,
      refreshing: false,
      token: "",
      startTime: "",
      endTime: "",
      station: "",
      urlPic: UrlPic,
      pageNum: 10,
      res: [],
      showNoData: false
    };
  },
  methods: {
    call(phone) {
      window.location.href = "tel://" + phone;
    },
    async initData(startTime, endTime, station) {
      // console.log("startTime", startTime);
      // console.log("endTime", endTime);
      // console.log("station", station);
      this.startTime = startTime;
      this.endTime = endTime;
      this.station = station;
      // /wastetransfer/wastetransfer/lists
      var url = "/wastetransfer/wastetransfer/lists";
      //  参数：starttime/waterstation/lasttime

      var starttime = startTime;
      var lasttime = endTime;
      var isExcel = "no";
      var pageNum = 1;
      var pageSize = this.pageNum;
      var waterstation = station;
      var day = 0;
      var advice = "";
      day = AppBace.dateTimeDev(starttime, lasttime);
      // day = AppBace.dateTimeDev(starttime, lasttime);
      var res = await AppBace.request(
        url,
        "POST",
        {
          starttime,
          lasttime,
          waterstation,
          pageNum,
          pageSize,
          isExcel
        },
        this.token
      );
      console.log(res);
      this.res = res;
      if (res.data.data.length === 0) {
        this.showNoData = true;
      } else {
        this.showNoData = false;
      }
      this.list = this.pictureFor(res.data.data);
      this.loading = false;
      // if (day === 0) {
      //   day = "24小时";
      // } else if (day < 0) {
      //   Toast("时间筛选有误，请重新选择");
      //   return;
      // } else {
      //   day = day + "天";
      // }
      // if (res.data.data.length === 0) {
      //   advice = ",当前水站信息数据为空，请调整区间或者查看其他站点";
      // }
      // Toast("数据为最近" + day + "的废液转运数据" + advice);
    },
    pictureFor(res) {
      for (let index = 0; index < res.length; index++) {
        var photo = res[index].photo;
        res[index].photo = photo === "" ? "" : photo.split(",");
      }
      return res;
    },
    browerPic() {
      ImagePreview({
        images: ["https://img.yzcdn.cn/1.jpg", "https://img.yzcdn.cn/2.jpg"],
        startPosition: 1,
        onClose() {
          // do something
        }
      });
    },
    imageShow(img, i) {
      console.log(img);
      ImagePreview({
        images: [img],
        startPosition: i,
        onClose() {
          // do something
        }
      });
    },
    onLoad() {
      // setTimeout(() => {
      if (this.refreshing) {
        this.list = [];
        this.refreshing = false;
        this.pageNum = 10;
        // this.initData(this.startTime, this.endTime, this.station);
      }

      // for (let i = 0; i < 10; i++) {
      //   this.list.push(this.list.length + 1);
      // }
      this.loading = false;
      this.pageNum += 10;
      this.initData(this.startTime, this.endTime, this.station);
      if (
        this.list.length === this.res.data.totalpage ||
        this.list.length === 0
      ) {
        this.finished = true;
      }
      // }, 1000);
    },
    onRefresh() {
      // 清空列表数据
      this.finished = false;

      // 重新加载数据
      // 将 loading 设置为 true，表示处于加载状态
      this.loading = true;
      this.onLoad();
      // this.initData(this.startTime, this.endTime, this.station);
    }
  },
  mounted() {
    this.token = sessionStorage.getItem("token");
    setTimeout(() => {
      this.showSkeleton = true;
    }, 2000);
  }
};
</script>
<style scoped>
.boxFlex {
  display: flex;
  font-size: 12px;
  font-weight: 500;
  align-items: center;
  /* border-bottom: 1px rgb(240, 240, 240) solid; */
  height: 30px;
}
.rowleft {
  display: flex;
  flex: 2;
  align-items: center;
  /* display: flex; */
  /* justify-items: center;
  justify-content: center; */
}
.rowleft div {
  height: 30px;
}
.rowright div {
  height: 30px;
}
.rowright {
  flex: 3;
}
.imageSizeWaste img {
  width: 80px;
  height: 80px;
}
.imgBox {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  flex-direction: row;
}
.box {
  width: 80px;
  margin-right: 10px;
}
.styleEp {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.borderBottom {
  /* border-bottom: 1px rgb(241, 241, 241) solid; */
  font-weight: 600;
}
.flexBox{
  display: flex;
  justify-content: center;
}
</style>


