<template>
  <div>
    <float-icons padding="10 10 60 10" class="icons-warp">
      <div class="float-icon-item" @click="handleIcons('Feedback')">
        <div class="historyForData">
          <span style="color:#ffffff">找回数据</span>
        </div>
        <!-- <span>左侧菜单</span> -->
      </div>
    </float-icons>
    <van-dialog v-model="show" title="找回数据" show-cancel-button>
      <div class="forData">
         确认找回 {{starttime}} 00:00:00~{{ lasttime}} 00:00:00的数据吗 ？（注意时分秒）
      </div>

    </van-dialog>
  </div>
</template>
 
<script>
import FloatIcons from "@/components/s-icons";
import AppBace from "../../../static/ajax";
import { Toast } from "vant";

export default {
  data() {
    return {
      token: "",
      show:false,
    };
  },
  components: {
    "float-icons": FloatIcons
  },
  props: ["lasttime", "starttime", "waterstation"],
  methods: {
    // 点击按钮
    async handleIcons(router) {
      this.show =true;
      this.token = sessionStorage.getItem("token");
      var url = "/station/water-data/request";
      var res = await AppBace.request(
        url,
        "POST",
        {
          lasttime: this.lasttime,
          starttime: this.starttime,
          waterstation: this.waterstation
        },
        this.token
      );
      console.log("res", res);
      if (res.data.message === "success") {
        // Toast("请求已经发送成功，请稍后查看数据信息！");
      } else {
        Toast("当前网络加载延迟，请重试！");
      }
      // this.$router.push(router);
      //   this.showBtn();
    },
    parant() {
      // alert(2);
      //   this.$parent.fatherMethod();
    }
  }
};
</script>
<style scoped>
.historyForData {
  background: #3572fe;
  /* color: blue; */
  font-size: 14px;
  padding: 5px;
  /* border: 1px solid #3572fe; */
  border-radius: 5px;
  box-shadow: 0px 0px 10px #ccc;
}
.icons-warp {
  border-radius: 25px;
  opacity: 0.8;
}
.float-icon-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  /* right: 20px; */
  /* width: 50px; */
  /* height: 50px; */
  /* background: #d3d3d3; */
  /* border-radius: 50%; */
  opacity: 1;
}
img {
  width: 25px;
  height: 25px;
  margin-bottom: 3px;
}
span {
  font-size: 9px;
  color: rgb(252, 250, 250);
}
.forData{
  padding: 10px;
  font-size: 14px;
  display: flex;
  justify-content: center;

}
</style>


